html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  padding-top: 60px;
}

:root {
  --underline-height: 0.8em;
  --transition-duration: 0.8s;
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: white;
  display: flex;
  align-items: center;
  justify-content: space-between; 
  padding: 12px 24px; 
  box-sizing: border-box; 
  z-index: 1000;
}


nav.black .underline {
  background: #7a5c45;
  border-radius: 100% 100% 0 0;
  height: calc(var(--underline-height) / 2);
}


.logo {
  margin-right: 40px;
}
.logo img {
  height: 80px;
  cursor: pointer;
}



.menu {
  position: relative;
  display: flex;
  gap: 24px;
  align-items: center;
  padding-left: 40px; 
}
.menu a {
  text-decoration: none;
  color: #7a5c45;
  padding: 8px 0;
}

.underline {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 3px;
  background: #7a5c45;
  transition: transform 0.3s ease, width 0.3s ease;
}

section {
  padding: 80px 16px;
  max-width: 1100px;
  margin: auto;
}

#hero {
  min-height: 80vh;
  display: flex;
  align-items: center;
  justify-content: left;
  text-align:center;
  
	background-image: url('../assets/images/oujda pic.jpg');
  	background-size: cover;   
  	background-position: center; 
  	background-repeat: no-repeat;
}
#hero::before {
  content: "";
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 122%;
  background: rgba(0,0,0,0.4); 
  z-index: 1;
} 
#hero > * {
  position: relative;
  z-index: 2; 
  color: white;
}

#culture {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}


h1 {
  font-size: 48px;
}

h2 {
  font-size: 32px;
}

p {
  line-height: 1.6;
  color: #7a5c45;
}
